import { Injectable } from '@angular/core';
import { Observable, BehaviorSubject } from 'rxjs';
import { VendorService } from '../core/vendor.service';
import { AreaService } from '../core/area.service';
import { TableState } from '../core/table-state';


@Injectable()
export class VendorManagerService {

  public state: TableState = {
    page: 1,
    pageSize: 10,
    searchTerm: '',
    sortColumn: '',
    sortDirection: ''
  }

  private _total$ = new BehaviorSubject<number>(1);
  get total$(): Observable<number> {
    return this._total$.asObservable();
  }

private originalList = [];
  private _vendorList$ = new BehaviorSubject<any[]>(null);
  
  get vendorList$(): Observable<any[]> {
    return this._vendorList$.asObservable();
  }

  private _areaList$ = new BehaviorSubject<any[]>(null);
  get areaList$(): Observable<any[]> {
    return this._areaList$.asObservable();
  }
  
  constructor(private vendorService: VendorService, private areaService: AreaService) { }



  createVendor(user: any): Observable<any> {
    const vendor = {
      name: user['name'],
      email: user['email'],
      address: user['address'],
      area: user['area'],
      mobile: user['mobile'],
      status: user['status'],
      gst_no: user['gst_no'],
      password: user['password'],
      contact_person_name: user['contact_person_name'],
      whatsapp_no: user['whatsapp_no'],
      restaurant_category: user['restaurant_category'],
      cuisine: user['cuisine'],
      state: user['state'],
      rating: user['rating'],
      branches: user['branches'],
      is_top_restaurant: user['is_top_restaurant'],
      image: user['image']
    };
    return this.vendorService.createVendor(vendor);
  }

  updateVendor(user: any): Observable<any> {
    const vendor = {
      vendor_id: user['vendor_id'],
      name: user['name'],
      email: user['email'],
      address: user['address'],
      area: user['area'],
      mobile: user['mobile'],
      status: user['status'],
      gst_no: user['gst_no'],
      contact_person_name: user['contact_person_name'],
      whatsapp_no: user['whatsapp_no'],
      restaurant_category: user['restaurant_category'],
      cuisine: user['cuisine'],
      state: user['state'],
      rating: user['rating'],
      branches: user['branches'],
      is_top_restaurant: user['is_top_restaurant'],
      image: user['image']

    };
    return this.vendorService.updateVendor(vendor);
  }

  deleteVendor(id: string): Observable<any> {
    return this.vendorService.deleteVendor(id);
  }

  getVendor(id: string): Observable<any> {
    return this.vendorService.getVendor(id);
  }


  getVendorList(): void {
    this.vendorService.getVendorList()
      .subscribe((data: any[]) => {
        console.log('Got Vendors for you: ', data);
        this._vendorList$.next(data);
        this.originalList = data;
        this._total$.next(data.length)
      });
  }

  getAreaList(): void {
    this.areaService.getAreaList()
      .subscribe((data: any[]) => {
        console.log('Got Areas for you: ', data);
        this._areaList$.next(data);
      });
  }

  searchText(): void {
    if (!this.state.searchTerm) {
      this._vendorList$.next(this.originalList);
      this._total$.next(this.originalList.length)
    } else {
      const searchTerm = this.state.searchTerm.toLocaleLowerCase();
      const filteredList = this.originalList.filter((item: any) => item['name'] && item['name'].toLocaleLowerCase().includes(searchTerm) ||
      item['email'] && item['email'].toLocaleLowerCase().includes(searchTerm) ||
      item['area'] && item['area'].toLocaleLowerCase().includes(searchTerm) ||
      item['mobile'] && item['mobile'].toLocaleLowerCase().includes(searchTerm) ||
      item['restaurant_category'] && item['restaurant_category'].toLocaleLowerCase().includes(searchTerm) ||
      item['cuisine'] && item['cuisine'].toLocaleLowerCase().includes(searchTerm) ||
      item['status'] && item['status'].toLocaleLowerCase().includes(searchTerm) );
      this._vendorList$.next(filteredList);
      this._total$.next(filteredList.length);
    }
  }
}
