import { Component, OnInit, OnDestroy, ChangeDetectorRef, ViewEncapsulation } from '@angular/core';
import { VendorManagerService } from '../vendor-manager.service';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { ConfirmDeleteComponent } from '../confirm-delete/confirm-delete.component';
import { from, Subject } from 'rxjs';
import { filter, takeUntil } from 'rxjs/operators';

@Component({
  selector: 'app-user-list',
  templateUrl: './user-list.component.html',
  encapsulation: ViewEncapsulation.None

})
export class UserListComponent implements OnInit, OnDestroy {
  private ngUnsubcribe = new Subject();
  // public vendorList$ = this.vendorManagerService.vendorList$;

  // public total$ = this.vendorManagerService.total$;
  // state = this.vendorManagerService.state;
  constructor(public vendorManagerService: VendorManagerService, private ModalService: NgbModal, private cdRef: ChangeDetectorRef) {
  }

  ngOnInit(): void {
    this.vendorManagerService.getVendorList();
  }

  ngOnDestroy() {
    this.ngUnsubcribe.next();
    this.ngUnsubcribe.complete();
  }


  deleteVendor(vendor: any): void {
    const modalRef = this.ModalService.open(ConfirmDeleteComponent, {
      size: 'lg',
      backdrop: 'static'
    });
    modalRef.componentInstance.vendor = vendor;
    from(modalRef.result)
      .pipe(
        filter((data: any) => data),
        takeUntil(this.ngUnsubcribe))
      .subscribe(() => this.vendorManagerService.getVendorList());
  }
  stateUpdate(): void {
    this.cdRef.detectChanges()
    console.log('fdf : ', this.vendorManagerService.state);
    
  }
}
